.tab__card {
  display: flex;
  padding-top: 76px;

  @media (max-width: $tablet) {
    flex-direction: column;
    padding-top: 10px;
  }
}

.tab__section {
  width: 25vw;
  margin-right: 4.8vw;

  @media (max-width: $tablet) {
    width: 97%;
    text-align: center;

    padding-bottom: 30px;
    margin-left: 0.5vw;
    margin-right: 1.5vw;
  }
}

.tab__caption {
  font-size: 55px;
  line-height: 90px;
  font-weight: 600;
  position: relative;
  margin: 0;
  letter-spacing: -0.23vw;
  padding-top: 9px;

  &::before {
    content: "";
    width: 9vw;
    height: 4px;
    background-color: #fe7865;
    position: absolute;
    z-index: 2;
    top: 113px;
  }

  @media (max-width: $tablet) {
    font-size: 30px;
    padding-top: 0;
    &::before {
      display: none;
    }
  }

  @media (max-width: $mobile) {
    font-size: 24px;
    line-height: 28px;
  }
}

.tab__block {
  font-size: 16px;
  line-height: 28px;
  color: $place-text-grey;
  padding-top: 50px;
  margin: 0;
  padding-left: 4px;
  letter-spacing: -0.47px;

  @media (max-width: $tablet) {
    padding-top: 0;
  }

  @media (max-width: $mobile) {
    padding-top: 30px;
    font-size: 16px;
    line-height: 28px;
  }
}

.tab__button {
  padding: 30px 35px;
  margin-top: 50px;
  display: block;
  width: 250px;

  @media (max-width: $tablet) {
    margin: 0 auto;
    padding: 18px 35px;
    margin-top: 18px;
    font-size: 10px;
  }
}

.tab__img {
  width: 430px;

  @media (max-width: $tablet) {
    width: 100%;
    height: 415px;
  }
}

.tab__reviews {
  position: absolute;
  top: 250px;
  left: 62.4%;
  right: 10%;
  width: 382px;
  background-color: $white;
  padding: 36px 30px 13px 30px;

  @media (max-width: $tablet) {
    top: 60%;
    left: 25%;
    padding: 29px 30px 13px;
  }

  @media (max-width: $mobile) {
    width: 83.75vw;
    left: 9%;
    top: 66%;
    bottom: 0;
    text-align: center;
    padding-bottom: 0;
  }
}

.tab__reviews-list {
  display: flex;
  justify-content: start;
  padding-left: 0;
  align-items: center;

  @media (max-width: $mobile) {
    flex-direction: column;
  }
}

.tab__reviews-img {
  width: 37px;
  height: 38px;
  margin-right: 16px;
}

.tab__reviews-caption,
.tab__reviews-block {
  margin: 0;
}

.tab__reviews-block {
  padding-bottom: 12px;
  font-size: 14px;
  line-height: 28px;
  color: rgba(44, 46, 63, 0.7);
  letter-spacing: -0.41px;

  @media (max-width: $tablet) {
    padding-bottom: 14px;
  }

  @media (max-width: $mobile) {
    padding-bottom: 20px;
  }
}

.tab__reviews-caption {
  padding-bottom: 34px;
  font-size: 30px;
  letter-spacing: -0.7px;
  font-weight: 500;

  &::before {
    content: "";
    width: 9vw;
    height: 0.73px;
    background-color: #fe7865;
    position: absolute;
    z-index: 2;
    top: 79px;
  }
}

.macedonia,
.montenegro,
.croatia {
  @media (max-width: $mobile) {
    display: none;
  }
}

.croatia {
  @media (max-width: $tablet) {
    display: none;
  }
}

.tab__section {
  padding-left: 5.3vw;

  @media (max-width: $tablet) {
    padding-left: 1.3vw;
  }
}
